.pagination {

    &__number {
        display: inline-block;
        font-size: 1.6rem;
        padding: 1rem;
        color: $color-primary;
        border: .1rem solid $color-primary;
        cursor: pointer;
        margin: .1rem;
        user-select: none;

        &__active {
            color: $color-white;
            background-color: $color-primary;
        }
        &:hover {
            color: $color-white;
            background-color: $color-primary;
        }
    }
}